    }
} */

@media screen and (max-width: 700px) {
    /* .web__navbar-links_container{
        display: none;
    } */
    
    .web__navbar{
        padding: 2rem 4rem;
    } 
}

@media screen and (max-width: 550px) {
    .web__navbar-menu{
        display: flex;
    }
    .web__navbar{
        padding: 2rem;

    } 
    .web__navbar-sign{
        display: none;
    }
    .web__navbar-menu_container{
        top: 20px;
        
    }
    .web__navbar-menu_container-links-sign{
        display: block;
    }
}
